.antarctic h1{
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
  color: $alice-blue;
  text-shadow: 0px 4px 4px $color-default-white 0.25;
  margin-bottom: 0;
  position: absolute;
  left: 198px;
  top: 283px;
}

.antarctic p {
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: $alice-blue;
  margin-top: 0;
  margin-bottom: 0;
  position: absolute;
  left: 198px;
  top: 388px;
}

@media (min-width:$tablet-size-min) and (max-width:$tablet-size-max) {
  .antarctic h1 {
    font-size: 50px;
    line-height: 61px;
   }
}

@media (max-width:$mobile-size-max) {
  .antarctic h1 {
    font-size: 36px;
    line-height: 125%;
    left: 15px;
    top: 185px;
  }

  .antarctic p {
    left: 15px;
    top: 281px;
  }
}
