.main-nav {
  display: flex; }

.main-nav__burger {
  display: none; }

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  align-self: center; }

.main-nav__link {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd; }

@media (max-width: 767px) {
  .main-nav__list {
