    flex-direction: column;
    align-items: center; }
  .is-mobile-menu {
    color: #011c40; } }

.trip {
  background-color: #f9fbfd;
  padding: 120px 0; }

.trip__wrapper {
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: 1fr 1fr; }

.trip__wrapper h2 {
  font-weight: 600;
  font-size: 28px;
  line-height: 150%;
  margin-top: 0;
  margin-bottom: 0;
  color: #011c40;
  grid-column: 1/2;
  grid-row: 1/2; }

.trip__wrapper p {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  margin-top: 0;
  margin-bottom: 0;
  grid-column: 1/2;
  grid-row: 2/-1; }

.trip__image {
  grid-column: 2/-1;
  grid-row: 1/-1; }

@media (max-width: 1023px) {
  .trip h2 {
    font-size: 26px;
    line-height: 140%; } }

@media (max-width: 767px) {
  .trip__wrapper {
    display: block; }
  .trip h2 {
    font-size: 24px; } }

